import Vue from 'vue'
import Router from 'vue-router'
import index from '@/components/home/index' //
import careers from '@/components/home/careers'//
import games from '@/components/home/games'//
import games_details from '@/components/home/games_details'//
import game_news from '@/components/home/game_news'//news
import about from '@/components/home/about'//
import recharge from '@/components/home/recharge'//
import RechargeDetail from '@/components/home/recharge_detail.vue'

Vue.use(Router)

export default new Router({
	routes: [
		{
			path: '/',
			redirect: '/index'
		},
		{
			path: '/index',
			component: index
		},
		// {//
		// 	path: '/careers',
		// 	name: 'careers',
		// 	component: careers
		// },
		{//
			path: '/games',
			name: 'games',
			component: games,
			children: [
				{ path: '/', component: games }
			]
		},
		{//
			path: '/recharge',
			name: 'recharge',
			component: recharge
		},
		{//
			path: '/games/*',
			component: games_details,
		},
		{//
			path: '/games_details',
			name: 'games_details',
			component: games_details
		},
		{//news
			path: '/game_news',
			name: 'game_news',
			component: game_news
		},
		{//
			path: '/about',
			name: 'about',
			component: about
		},
		{
			path: '/recharge/:gameId',
			name: 'rechargeDetail',
			component: RechargeDetail
		}
	]
})



