<template>
  <article class="careers">
      <!---->
      <section class="banner container-fluid">
            <img src="../../../static/img/join-bg.jpg" alt="">
            <div class="title">
              <p class="p1">CAREERS</p>
              <p class="p2"></p>
            </div>
      </section>

    <!---->
    <section class="job">
      <div class="title text-center">
        <h3 class="text-center"></h3>
      </div>
    </section>

    <div class="container">
      <!---->
      <div class="job-list" v-for="v in 5">
          <div class="j-header clearfix" @click="toggleShow($event)">
              <div class="pull-left job-name">
                  <p></p>
                  <p>Art Director</p>
                  <p class="info">
                    <span class="s1"></span> <span class="s2">1</span></p>
              </div>
              <div class="pull-right arrow"><i class="glyphicon glyphicon-menu-down"></i></div>
          </div>
          <div class="content" style="display:none">
                <p></p>
                <ul>
                  <li>1</li>
                  <li>2</li>
                  <li>3</li>
                  <li>4</li>
                  <li>5</li>
                </ul>
                <p></p>
                <ul>
                  <li>1JavascriptES6Node.js</li>
                  <li>2socket</li>
                  <li>3mysqlGit</li>
                  <li>4</li>
                  <li>5</li>
                </ul>
          </div>
      </div>

    </div>
  </article>
</template>

<script>
  export default {
    data() {
      return {
        activeIndex: 0,
        invite:{
          first_card:{
            sketch:[],
            particulars:[]
          }
        }
      }
    },
    methods:{
        toggleShow(event){
            let currentTarget = event.currentTarget;
            $(currentTarget).next().slideToggle();
            
        }
    },
    mounted(){
        this.toggleShow();
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
  .careers {
    .orange {
      color: #ff9d01;
    }
		// 
		.banner {
			padding:0;
			position: relative;
      background-color: #2b292a;
      img {
        display:block;
        margin:0 auto;
        max-width:1920px;
      }
			.title {
				position: absolute;
				text-align: center;
				top:50%;
				left:50%;
				transform:translate(-50%,-50%);
				color:#fff;
				font-size:38px;
				.p2 {
					font-size:24px;
				}
				@media screen and (max-width:767px){
					font-size:4vw;
					.p2 {
						font-size:3vw;
					}
				}
			 }
		}
    // 
    .job {
      .title {
        padding: 75px 0 60px;
        border-bottom: 1px solid #eee;
        h3 {
          text-align: center;
          padding-bottom: 25px;
          font-size: 30px;
          position: relative;
          &:after {
            content: "";
            width: 80px;
            height: 3px;
            background: #ff9a24;
            position: absolute;
            bottom: 0;
            left: 50%;
            transform: translateX(-50%);
          }
        }
      }
      // 
      @media screen and (max-width:414px) {
        .title{
          padding: 25px 0 10px;
        }
        .title h3 {
          font-size:22px;
        }
      }
    }
    // 
    .job-list {
          padding-bottom:22px;
          padding-top:10px;
          border-bottom:1px solid #e7e7e7;
        .j-header {
          color:#444444;
          cursor:pointer;
           .job-name {
              font-size:22px;
              p.info {
                font-size:15px;
                span.s1 {
                   padding-right:20px;
                }
                span.s2 {
                   color:#fb9934;
                }
              }
           }
           .arrow {
              font-size:30px;
              color:#696969;
           }
           
        }
        .content {
          overflow: hidden;
           p {
             margin-top:12px;
           }
        }
        // 
        @media screen and (max-width:414px) {
           .j-header .job-name {
              font-size:16px;
                p.info {
                  font-size:12px;
                }
            }
           .j-header .arrow {
              font-size:20px;
           }
        }
    }
    

  }
</style>



